import { ApiService } from './../api.service';
import { FileService } from './../service/file.service';
import { Component, Input, Output, EventEmitter } from '@angular/core';
import { FileElement } from './model/element';
import { Properties } from './model/properties';
import { MatMenu, MatMenuTrigger } from '@angular/material/menu';
import { Observable } from 'rxjs/Observable';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import {MatCheckboxModule} from '@angular/material/checkbox';
import { NewFolderDialogComponent } from './modals/newFolderDialog/newFolderDialog.component';
import { RenameDialogComponent } from './modals/renameDialog/renameDialog.component';
import { PropertiesDialogComponent } from './modals/propertiesDialog/propertiesDialog.component';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';

@Component({
  selector: 'file-explorer',
  templateUrl: './file-explorer.component.html',
  styleUrls: ['./file-explorer.component.css']
})
export class FileExplorerComponent {
  constructor(public dialog: MatDialog,public MatCheckboxModule: MatCheckboxModule,public fileService: FileService) {}
 
  @Input() fileElements: FileElement[];
  @Input() properties;
  @Input() thumb_url: string = 'https://pcwest.remotepc.com/v1/mohsin/my_machine/thumb/';
  @Input() canNavigateUp: string;
  @Input() path: string;
  @Input() search: string;
  @Input() searchSubfolders: boolean = false;

  @Output() folderAdded = new EventEmitter<{ name: string }>();
  @Output() elementRemoved = new EventEmitter<FileElement>();
  @Output() elementDownloaded = new EventEmitter<FileElement>();
  @Output() elementSearched = new EventEmitter();
  @Output() elementProperties = new EventEmitter();  
  @Output() elementRenamed = new EventEmitter<FileElement>();
  @Output() elementMoved = new EventEmitter<{ element: FileElement; moveTo: FileElement }>();
  @Output() navigatedDown = new EventEmitter<FileElement>();
  @Output() navigatedUp = new EventEmitter();

  deleteElement(element: FileElement) {
    console.log('File Explorer -> Deleting file: '+element.name);
    this.fileService.delete(element);
    setTimeout(() => 
    {
      this.elementRemoved.emit(element);    
    },
    2000);
    
  }
  downloadElement(element: FileElement) {
    console.log('File Explorer -> Downloading file: '+element.parent+'/'+element.name+' isFolder:'+element.isFolder);
    if(!element.isFolder){
    this.fileService.download(element.name);
    this.elementDownloaded.emit(element);
    }
  }
  downloadZip(element: FileElement) {
    console.log('File Explorer -> Downloading Zip: '+element.name+' isFolder:'+element.isFolder);
    if(element.isFolder){
      let newpath = ((!this.path || this.path=='')) ? '/' : this.path;
      this.fileService.downloadZip(newpath,element.name);
      this.elementDownloaded.emit(element);
    }
  }
  includeSubfolders(includecheck: boolean) {
    this.searchSubfolders=includecheck;
    console.log('File Explorer -> Search includes subfolders?: '+includecheck);

  }
  searchTerm(term: string = '') {
    console.log('File Explorer -> Searching file: '+term);
    let newpath = ((!this.path || this.path=='')) ? '/' : this.path;
    this.fileService.queryInFolder(newpath,term,this.searchSubfolders);
    this.elementSearched.emit(term);
  }
  navigate(element: FileElement) {
    if (element.isFolder) {
      this.navigatedDown.emit(element);
    }
  }

  navigateUp() {
    this.navigatedUp.emit();
  }

  moveElement(element: FileElement, moveTo: FileElement) {
    this.fileService.update(element.id, { parent: moveTo.id });
    setTimeout(() => 
    {
      this.elementMoved.emit({ element: element, moveTo: moveTo });    
    },
    2000);
  }

  openNewFolderDialog() {
    let dialogRef = this.dialog.open(NewFolderDialogComponent);
    dialogRef.afterClosed().subscribe(res => {
      if (res) {
        this.folderAdded.emit({ name: res });
      }
    });
  }
  openPropertiesDialog(element: FileElement) {
    let newpath = ((!this.path || this.path=='')) ? '/' : this.path;
    //this.elementProperties.emit({ name: newpath });
    //console.log(this.fileService.getProperties(newpath, element));
    this.fileService.getProperties(newpath, element).then(
      (properties) => {console.log(properties);let dialogRef = this.dialog.open(PropertiesDialogComponent, {
        width: '600px',
        data: {
         properties
        }
      });
      dialogRef.afterClosed().subscribe(res => {
        if (res) {
          element.name = res;
          //this.fileService.rename(element.id, { name: element.name });
          //this.elementRenamed.emit(element);
        }
      });
  },
      (err) => console.error(err)
    );

//console.log(this.properties);
    
  }
  openRenameDialog(element: FileElement) {
    let dialogRef = this.dialog.open(RenameDialogComponent);
    dialogRef.afterClosed().subscribe(res => {
      if (res) {
        element.name = res;
        //this.fileService.rename(element.id, { name: element.name });
        this.elementRenamed.emit(element);
      }
    });
  }

  openMenu(event: MouseEvent, viewChild: MatMenuTrigger) {
    event.preventDefault();
    viewChild.openMenu();
  }
}



